*{box-sizing:content-box; outline:none;}
html{  
    -moz-text-size-adjust:100%;
     -ms-text-size-adjust:100%;
     -o-text-size-adjust:100%;
     -webkit-text-size-adjust:100%;
     text-size-adjust:100%;
}
:root{ 
    font-size: 10px; 
    line-height: 1.36;
    // color: var(--text);  
    // font-family: 
     
    //fonts
    --common-font: 'HeliosC', sans-serif; 
    --title-font: 'Unbounded', sans-serif; 
    //colors
    --black: #000000;
    --light: #FFFFFF;
    --main-blue: #0067B1;
    --blue: #74B2E2;
    --soft-blue: #0096D6;
    --light-blue: #D4EFFC;
    --green: #009D75;
    --soft-green: #67C18A;
    --completed: #69C28B;
    --khaki: #49842B;
    --aquamarin: #01B8BC;
    --bg: #EFF9FE;
    --red: #E31837;
    --dark-grey: #58595B;
    --grey: #939598;
    --soft-grey: #E6E7E8;
    --bg-elem: #F0F3F9;
    --hover-menu: rgba(0, 103, 177, 0.1);
    --orange: #F3712C;
    --purple: #7E79B9;
    --dark-purple: #706BAB;
    --soft-btn-blue: #F2F9FD;
    --blue-btn-text: #9BB6BC;
    --orange-alert: #FEF1EA;
    --blue-alert: #E6F0F8;
    --violet: #CF5173;

    --great-transition: all 200ms ease;
}
 
body{ 
    position: relative;
    flex-direction: column;
    display: flex;
    background-color: var(--bg); 
    margin: 0 ; 
    padding:0; 
    -webkit-font-smoothing: antialiased; -moz-font-smoothing: antialiased; -o-font-smoothing: antialiased; 
    min-height:100vh;   
    color: var(--black); 
    font-size: 1.6rem;
    line-height: 1.4;
    font-weight: 400;
    font-family: var(--common-font);
}
  

h1, h2, h3, h4, h5, h6, p{display:block; margin:0; padding:0; font-weight:400;}
.h1, .h2, .h3, .h4, .h5, .h6{margin:0; padding:0; float:none;}
img, input, textarea, button, a{border:none; outline:none;}



input, textarea, button, a{
    // font-family: 'Nunito', sans-serif;   
    font-weight: normal; 
    font-size: 1.11rem; 
    line-height: 1;
}

table{border-spacing:0; border-collapse: collapse; margin: 0; padding: 0;}
td, th{vertical-align:top; word-wrap:break-word;}
ul, ol, nav, dl{margin:0; padding:0; }
a{text-decoration:none; font-size: inherit; line-height: inherit; /*color: var(--blue);*/ font-weight: inherit}
a:hover{text-decoration:underline;}
button{display:block; cursor:pointer; box-sizing:border-box; padding:0;}
button, .button{ text-decoration:none !important;}
//button:hover, .button:hover{transition: opacity 1.2s, visibility 0s linear 1.2s; opacity:0.7;}
address{font-style: normal;}

.inner{position:relative; /*max-width:1545px;*/; margin:0 auto; box-sizing: border-box;} 
section{position:relative;}
main{position:relative; clear:both;   box-sizing: border-box;}
header, nav, section, article, aside, footer, main{display:block;}
