.dvfu-tooltip {
  position: relative;
  width: 2rem;
  height: 2rem;
  z-index: 20;
}

.dvfu-tooltip__body {
  position: absolute;
  width: auto;
  padding: 1rem;
  border-radius: 4px;
  top: 120%;
  left: 50%;
  width: 200px;
  background-color: var(--light);
  border: 1px solid var(--soft-blue);
  font-size: 14px;
  box-sizing: border-box;
  transform: translateX(-50%);
  transition: opacity 200ms ease;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
}
.dvfu-tooltip__body--left {
  left: -10px;
  transform: translateX(0);
}
.dvfu-tooltip__body--right {
  left: auto;
  right: -10px;
  transform: translateX(0);
}
.dvfu-tooltip__body--top {
  top: auto;
  bottom: 120%;
}

.dvfu-tooltip__trigger {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 0;
  border-radius: 50%;
  background-color: var(--grey);
  mask-image: url(../img/icons/query-icon.svg);
  mask-size: contain;
  mask-position: center center;
  mask-repeat: no-repeat;
  transition: var(--great-transition);
  cursor: pointer;
}
.dvfu-tooltip__trigger:hover {
  background-color: var(--soft-blue);
}
.dvfu-tooltip__trigger:hover ~ .dvfu-tooltip__body {
  opacity: 1;
  visibility: visible;
}

.columns-box {
  display: grid;
  grid-gap: 2.4rem;
}
.columns-box--series-and-namber {
  grid-template-columns: 1fr 1.5fr;
  grid-gap: 1.6rem;
}
.columns-box--two-col {
  grid-template-columns: repeat(2, 1fr);
}
.columns-box--three-col {
  grid-template-columns: repeat(3, 1fr);
}
.columns-box--four-col {
  grid-template-columns: repeat(4, 1fr);
}
.columns-box--estimation {
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.6rem;
}

