.great-shadow{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .4);
    opacity: 0;
    visibility: hidden;
    transition: var(--great-transition);
    z-index: 200;
    &.show{
        opacity: 1;
        visibility: visible;
    }
}